<template>
  <div class="user" v-if="user">
    <div class="user__left">
      <img :src="user.avatar_url" alt="">
      <a :href="user.html_url" class="user__btn">ПОСЕТИТЬ</a>
    </div>
    <div class="user__rigth">
      <p>{{user.login}}</p>
      <p>Репозиториев: <span>{{ user.public_repos }}</span></p>
      <p>Создан: <span>{{ new Date(user.created_at).toLocaleDateString() }}</span></p>
      <p>Подписщиков: <span>{{ user.followers }}</span></p>
      <p>Подписок: <span>{{ user.following }}</span></p>
    </div>
  </div>
  <div class="sort">
    <h2 class="sort__title">Сортировка</h2>
    <div class="sort__box">
      <button 
      class="sort__box-btn" 
      v-for="(btn, idx) in btns" 
      :key="idx"
      :class="{active: isActive == idx}"
      @click="sortRepos(idx, btn.sortType)"
      >
      {{ btn.name }}
      </button>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  computed:{
    ...mapState(['user'])
  },
  data(){
    return{
      btns:[
        {name: 'ИМЯ', sortType: 'name'},
        {name: 'ЗВЕЗДЫ', sortType: 'stargazers_count'},
        {name: 'ДАТА', sortType: 'created_at'},
      ],
      isActive: 0
    }
  },
  methods: {
    ...mapMutations(['sort']),
    sortRepos(index, sortType){
      this.isActive = index
      this.sort(sortType)
    }
  },
}
</script>

<style lang="scss">

.sort{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px 0;
  gap: 10px;
  &__title{
    color: #3F3F3F;
    font-size: 18px;
    font-weight: 700;
    line-height: 120%;
    text-transform: capitalize;
  }
  &__box{
    background: #E0E0E0;
    border-radius: 4px;
    overflow: hidden;
    padding: 2px;
    &-btn{
      border: none;
      outline: none;
      background: transparent;
      padding: 6px 36px;
      color: #3F3F3F;
      font-size: 16px;
      font-weight: 700;
      line-height: 120%;
      text-transform: capitalize;
      cursor: pointer;
      border-radius: 4px;
      &.active{
        background: #ffff;
      }
    }
  }
}
</style>